}
.caja-fav{
    height: 110px;
    z-index: 1;
    overflow-y: scroll;

}

a{
    text-decoration: none;
    color: ivory;
}

.ventana{
  height: 450px;
  width: 350px;
  position: fixed;
  bottom: 10%;
  margin: 0% auto;  
}

h4 {
    font-size: 18px;
    text-align: center;
    text-transform:uppercase;
    text-decoration: underline;
    
}
footer{
    display: flex;
    text-align: center;
    font-size: 50px;

    justify-content: space-around;
    background-color: rgb(12, 12, 4);
    border-radius: 5px;
    padding: 5px;
    
}
.facebook :hover{
    transform: scale(1.15);

}
.github :hover{
